@import './../../styles/index.scss';

.servicesWrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: inline-block;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 1)),
    url('./../../assets/images/services-bg4.jpg');
  background-position: center;
  background-size: cover;
  animation: position 150s linear infinite;
  padding-bottom: 100px;
  display: block;

  @media screen and (max-width: $phone) {
    padding-bottom: 30px;
  }
}

@keyframes position {
  50% {
    background-position: 0 70px;
  }
}

.imgContainer {
  transform: rotate(180deg);
  overflow: hidden;
}

.imgContainer img {
  display: block;
  overflow: hidden;
}

.serviceContainer {
  // box-shadow: 20px 20px 20px 0px rgba(0, 0, 0, 0.5);
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: rgba(255, 255, 255, 0.1);
  margin-bottom: 100px;
  padding-left: 30px;
  padding-right: 30px;

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  @media screen and (max-width: $phone) {
    margin-bottom: 80px;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.arrow {
  margin: 0 auto;
  display: block;
  width: 100px;
  height: 100px;

  @media screen and (max-width: $phone) {
    width: 60px;
    height: 60px;
  }
}
