@import './../../../styles/index.scss';

.images {
  margin: 0 auto;
  width: 100%;
  max-width: 1300px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 50px;

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 40px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 30px;
  }

  @media screen and (max-width: $tablet) {
    grid-template-columns: 1fr;
    row-gap: 15px;
  }
}

.blockContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.block {
  position: relative;
  width: 100%;
  max-width: 400px;
  height: 300px;
  display: flex;
  align-items: flex-end;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: $t-normal;
  box-shadow: 20px 20px 20px 0px rgba(0, 0, 0, 0.5);

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    max-width: 600px;
    height: 400px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    max-width: 500px;
    height: 300px;
  }

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    max-width: 100%;
    height: 400px;
  }

  @media screen and (max-width: $phone) {
    max-width: 100%;
    height: 270px;
  }
}

.titleContainer {
  width: 100%;
  background-color: $c-header;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.serviceTitle {
  color: $c-white;
}

.buttonContainer {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  opacity: 0;
  transition: $t-normal;
  cursor: pointer;
}

.buttonContainer:hover {
  opacity: 1;
}

.buttonContainer {
  background-color: $c-header;
}

.serviceButton {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: $c-link-hover;
  color: $c-white;
  padding: 16px 26px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease;
  transition: $t-normal;
}

.buttonContainer:hover .serviceButton {
  opacity: 1;
}

.serviceButton:hover {
  background-color: $c-link-active;
}
.serviceButton:active {
  background-color: rgb(23, 23, 23);
}

.btnText {
  letter-spacing: 1px;
}
