@import './../../styles/index.scss';

.bannerWrapper {
  position: relative;
  width: 100%;
  height: 135vh;
  background: linear-gradient(to bottom, rgb(3, 6, 6) 1%, rgba(1, 1, 1, 0.6)),
    url('./../../assets/images/bg-2.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    height: 100vh;
    background-position: left top;
  }

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    height: 130vh;
    background-position: left top;
  }

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    height: 110vh;
    background-position: left top;
  }

  @media screen and (min-width: 400px) and (max-width: $phone) {
    background-position: left top;
  }

  @media screen and (max-width: 400px) {
    background-position: -50px -30px;
  }

  @media screen and (max-height: 700px) {
    height: 170vh;
  }
}

@keyframes position {
  50% {
    background-position: 0 30px;
  }
}

.banner {
  padding-top: 250px;
}

.titleContainer {
  backdrop-filter: blur(10px);
  padding-top: 20px;
  padding-bottom: 20px;
}

.buttons {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 110px;

  @media screen and (max-width: $tablet) {
    flex-direction: column;
    gap: 50px;
  }
}

.link {
  padding: 10px 30px;
  display: flex;
  align-items: center;
  gap: 10px;
  opacity: 0.9;
  color: $c-white;
  font-weight: 600;
  border: 1px solid $c-white;
  border-radius: 30px;
  transition: $t-normal;
}

.link:hover {
  box-shadow: none;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  color: white;
  transform: scale(1.1);
}

.link:active {
  background-color: $c-link-active;
}

.links {
  display: flex;
  gap: 10px;
  flex-direction: column;
}

.linkFirst {
  display: flex;
  align-items: center;
  gap: 4px;
  // padding: 19px 36px;
  color: $c-white;
  font-size: 20px;
  transition: $t-normal;

  @media screen and (min-width: 385px) and (max-width: 805px) {
    font-size: 18px;
  }

  @media screen and (min-width: 356px) and (max-width: 395px) {
    font-size: 16px;
  }

  @media screen and (max-width: 356px) {
    font-size: 16px;
    flex-direction: column;
    align-items: start;
  }
}

.linkFirst span {
  border-bottom: 2px solid transparent;
}

.imgContainerSecond {
  position: absolute;
  display: block;
  bottom: 0;
  overflow: hidden;
}

.imgContainerSecond img {
  display: block;
  overflow: hidden;
}

.textLink {
  display: flex;
  align-items: center;
}

.linkFirst:hover {
  transform: scale(1.1);
}

.linkFirst .linkSecond {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.linkSecond {
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: $phone) {
    font-size: 14px;
  }
}

.iconContainer {
  width: 24px;
  height: 24px;

  @media screen and (max-width: $phone) {
    width: 20px;
  }
}

.icon {
  animation: icon 5s ease-in-out;
}

.arrow {
  width: 40px;
  transform: rotate(-90deg);
}

.underline {
  border-bottom: 2px solid red !important;
}

.arrowDown {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 50px;
  transition: $t-normal;

  @media screen and (max-width: $phone) {
    width: 60px;
    height: 60px;
    margin-top: 30px;
  }
}
